
import { Link, useMatch, useResolvedPath } from "react-router-dom";
import assets from '../assets/export';
export default function Navbar(){
    return(
        <header className="py-5">
            <div className="px-0 md:px-4">
                <nav className="relative z-50 flex justify-between w-full">
                    <div className="flex items-center gap-3 md:gap-8">
                        <Link aria-label="Home" to="/" className="w-10 h-10">
                           <img src={assets.logo} alt="home" />
                        </Link>
                        <div className="flex mx-1 text-white capitalize md:text-lg md:mx-0 md:gap-5">
                            <CustomLink to="/news" className="transition-all hover:underline hover:underline-yellow hover:text-yellow">news</CustomLink>
                        </div>
                        <div className="flex mx-1 text-white capitalize md:text-lg md:mx-0 md:gap-5">
                            <CustomLink to="/releases" className="transition-all hover:underline hover:underline-yellow hover:text-yellow">releases</CustomLink>
                        </div>
                    </div>
                    <div className="flex items-center md:gap-6">
                        <Link to="/invite" className="flex items-center w-7 h-7">
                            <img src={assets.discord} alt="discord" className="w-full"/>
                        </Link>
                        <Link to="/releases" className="items-center justify-center hidden font-bold capitalize rounded md:flex bg-yellow w-36 h-14">get client</Link>
                    </div>
                </nav>
            </div>
        </header>
    )
}
function CustomLink({ to, children, ...props }){
    const resolvedPath = useResolvedPath(to);
    const isActive = useMatch({ path: resolvedPath.pathname, end:true })
    return(
        <Link to={to} {...props} className={isActive === to ? "active" : ""}>
            {children}
        </Link>
    )
}  