import { Link } from "react-router-dom";
export default function home(){
    const btn = 'flex items-center justify-center h-full transition-all duration-300 ease-in-out border-2 rounded cursor-pointer';
    const importantXL = 'text-3xl text-yellow lg:text-4xl';
    const importantLG = 'lg:text-lg text-yellow';
    return (
        <main className="flex flex-row w-full md:pt-18 lg:pt-36">
            <section className="z-50 w-full h-full lg:w-1/2 lg:mr-8">
                <h2 className="mb-4 text-sm font-bold text-yellow">By: Word &amp; Irrvlo</h2>
                <h1 className="text-2xl font-bold text-lightgray lg:text-3xl">
                    <span className="text-3xl text-white lg:text-4xl">Nameless Client</span>
                    , a desktop application for <a href="https://kirka.io" target="_blank" rel="noreferrer" className={`${importantXL} underline`}>Kirka.io.</a> 
                    {" "}Made for the <span className={importantXL}>best user experience</span> possible, with lots of <span className={importantXL}>features</span> and a <span className={importantXL}>clean UI.</span>
                </h1>
                <p className="mt-5 text-ms text-subgray">
                    Features will include <span className={importantLG}>Unlimited FPS, Resource Swapper, Custom Badges,</span> and much more to come! Join our <a href="/invite" target="_blank" className={`${importantLG} underline`}>Discord</a> Server for updates, support, and leaks!
                </p>
                <div className="flex flex-row w-full h-16 mt-5 font-bold">
                    <CustomLink to="/releases" className={`${btn} w-1/2 mr-4 border-transparent bg-yellow text-dark hover:bg-transparent hover:border-yellow hover:text-yellow`}>Download</CustomLink>
                    <CustomLink to="/releases" className={`${btn} w-5/12 text-yellow border-yellow hover:bg-yellow hover:border-yellow hover:text-dark`}>Extension</CustomLink>
                </div>
            </section>
            <section className="hidden w-1/2 h-full lg:flex">
                <img id="preview" src="https://media.discordapp.net/attachments/919511402853580860/1062181737175715910/image.png" alt="preview"/>
            </section>
        </main>
    )
}
function CustomLink({ to, children, ...props }){
    return(<Link to={to} {...props}> {children} </Link>)
} 