export default function error(){
    const origin = window.location.origin;
    const path = window.location.pathname;
    return(
        <main className="w-full h-full flex items-center justify-center mt-40">
            <section className="flex flex-row">
            <h1 className="text-blue font-bold text-5xl px-5">404</h1>
            <div className="border-l-2 border-l-subgray pl-5 h-min">
                <h1 className="text-white text-5xl font-bold">Page Not Found</h1>
                <p className="text-lightgray text-lg">Please check the URL in the address bar and try again</p>
                <p className="text-subgray text-sm">{origin}<span className="text-yellow">{path}</span></p>
            </div>
            </section>
        </main>
    )
}