export default function home() {
  return (
    <div class="fixed z-0 flex items-center w-full h-full">
      <div id="pattern" class="absolute right-0 w-1/2 h-full bg-repeat"></div>
      <div id="noise" class="absolute w-full h-full bg-repeat opacity-50"></div>
      <div
        id="glow"
        class="absolute w-0 h-0 scale-110 opacity-30 left-56"
      ></div>
    </div>
  );
}
